#loading.black {
    background: var(--black);
}

#loading.light {
    background: var(--light_bg);

    .loading__text span {
        color: var(--black);
    }
}

#loading.loading__active {
    opacity: 1;
    // transition: all 1.6s;
}

#loading {
    opacity: 0;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--black);
    z-index: 99999;
    transition: all 1.6s;
}

.loading__text {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    text-align: center;
    width: 100%;
    height: 100px;
    line-height: 100px;

    span {
        display: inline-block;
        margin: 0 5px;
        color: var(--white);
    }

