    // }
    // span:nth-child(5){
    //     filter : blur(0px);
    //     animation: blur-test 1.5s 0.8s infinite linear alternate;
    // }
    // span:nth-child(6){
    //     filter : blur(0px);
    //     animation: blur-test 1.5s 1s infinite linear alternate;
    // }
    // span:nth-child(7){
