    // }

    // $blurX: 0;

    // @for $i from 1 through 7 {
    //     span:nth-child(#{$i}){
    //         filter : blur(0px);
    //         animation: blur-text 1.5s #{$blurX}s infinite linear alternate;
    //         $blurX: $blurX + 0.2;
    //     }
