#footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    color: var(--white);
    border-top: 1px solid var(--dark_border);
    display: flex;
    background-color: var(--dark_bg);
    align-items: center;
    font-family: "Rajdhani";
    z-index: 1000;
    div {
        text-align: center;
        padding: 1.45rem;

        a {
            padding: 0 0.6em;
            position: relative;

            &::before {
                content: '';
                position: absolute;
                left: 0.6rem;
                bottom: 0;
                width: calc(100% - 1.2rem) ;
                height: 1px;
                background: var(--white);
                transform: scaleX(0);
                transition: all 0.3s ease;
            }
            &:hover::before {
                transform: scaleX(1);
            }
        }
    }
    h4, ul {
        display: inline-block;
    }
    // a {
    //     position: relative;

    //     &::before {
    //         content: '';
    //         position: absolute;
    //         left: 0;
    //         bottom: 0;
    //         width: 100%;
    //         height: 1px;
    //         background: #fff;
    //         transform: scaleX(0);
    //         transition: all 0.3s ease;
    //     }
    //     &:hover::before {
    //         transform: scaleX(1);
    //     }
    // }
    h4 {
        font-weight: 600;
        padding: 0 0.6em;
        text-transform: capitalize;
    }
    div:nth-child(1) {
        flex: 1 1 25%;
        border-right: 1px solid var(--dark_border);
    }
    div:nth-child(2) {
        flex: 1 1 20%;
        border-right: 1px solid var(--dark_border);
    }
    div:nth-child(3) {
        flex: 1 1 55%;
        li {
            display: inline;
            a{
                display: inline-block;
                padding: 0 0.6em;
            }
        }
    }
}

.light#footer {
    background-color: var(--light_bg);
    border-top-color: var(--light_border);

    h4 {
        color: var(--black);
    }

    a {
        color: var(--black);

        &::before {
            background: var(--black);
        }
    }
    div {
        border-right: 1px solid var(--light_border);
    }
}

//미디어 쿼리
@media (max-width: 1008px){
    #footer {
        div:nth-child(1) {
            h4 {
                display: none;
            }
        }
        div:nth-child(2) {display: none;}
        div:nth-child(3) { display: none;}

    }
}

//푸터 애니메이션
#footer {
    bottom: -100%;
}